.card__subtitle {
  margin: 0;
  padding: 25px 21px 25px 15px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 640px) {
  .card__subtitle {
    padding: 24px 20px 26px 20px;
  }
}
