.profile {
  margin: 0 auto;
  margin-top: 40px;
  padding: 0;
  max-width: 880px;
  display: flex;
  gap: 30px;
  align-items: center;
}

@media (max-width: 940px) {
  .profile {
    flex-direction: column;
    padding: 0;
    margin-top: 42px;
    max-width: 282px;
    gap: 27px;
  }
}
